body {
    background-color: #ffeaa7;
    min-height: 80vh;
    padding: 2rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    color: hsl(198, 1%, 29%);
    font-family: "Architects Daughter", cursive;
    text-align: center;
    font-size: 160%;
  }
  
  .container {
    width: 100%;
    height: auto;
    min-height: 700px;
    max-width: 450px;
    min-width: 450px;
    word-break: break-all;
    background: #f1f5f8;
    background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
    background-size: 25px 25px;
    border-radius: 20px;
    box-shadow: 4px 3px 7px 2px #00000040;
    padding: 1rem .4rem;
    box-sizing: border-box;
  }
  .heading {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0.2rem;
  }
  .heading h1 {
    transform: rotate(2deg);
    padding: 0rem 1rem;
    border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
    background-color: #fdcb6e;
    font-size: 2rem;
  }
  
  .form input {
    box-sizing: border-box;
    background-color: transparent;
    padding-top: 0rem;
    padding-bottom: .2rem;
    border-bottom-right-radius: 15px 3px;
    border-bottom-left-radius: 3px 15px;
    border: solid 3px transparent;
    border-bottom: dashed 3px #fdcb6e;
    font-family: "Architects Daughter", cursive;
    font-size: 1rem;
    color: hsla(260, 2%, 25%, 0.7);
    width: 70%;
    margin-bottom: 20px;
  }
  
  button {
    padding-left: 1;
    border: none;
    font-family: "Architects Daughter", cursive;
    text-decoration: none;
    padding-bottom: 3px;
    border-radius: 5px;
    font-size: 1.2rem;
    ;
  }
  button span {
    background: #f1f5f8;
    display: block;
    padding: 0.3rem 1rem;
    border-radius: 5px;
    border: 2px solid hsl(198, 1%, 29%);
  }
  
  .toDoItem {
    display: flex;
    position: relative;
  }
  
  .toDoItem:hover {
    cursor: pointer;
  }
  
  li {
    text-align: left;
    position: relative;
    padding: 0.5rem;
    padding-right: 0rem;
  }
  
  li span {
    position: relative;
    left: -1.2rem;
  }
  
  .cancel {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 9px;
    padding-right: 0.5rem;
  }
  
  @media screen and (max-width: 768px) {
    body {
      font-size: 180%;
    }
    .container {
      min-width: 42vh;
      min-height: 90vh;
    }
    .heading h1 {
      font-size: 2rem;
    }
    ul {
      font-size: 1.5rem;
      padding-top: 0px;
      margin-top: -6px;
    }
  }